#car_container.myScroll {
  width: 100%;
  padding: 300px 0;
  height: 100vh;
  background: #000;
  position: relative;
  overflow: hidden;

  &.on {
    .car_wrap {
      right: 12%;
      .left_tire {
        transform: rotate(0);
      }

      .right_tire {
        transform: rotate(0);
      }
    }
  }

  .car_wrap {
    width: 1344px;
    position: absolute;
    right: -70%;
    transition: all 0.8s;

    > div {
      position: absolute;
      transition: all 0.8s;
    }

    .car_body {
      left: 0;
      top: 0;
    }

    .left_tire {
      left: 139px;
      top: 130px;
      transform: rotate(1440deg);
    }

    .right_tire {
      right: 120px;
      top: 123px;
      transform: rotate(1440deg);
    }
  }
}
