#slide {
  width: 100%;
  height: 1080px;

  .inner {
    width: 1460px;
    height: 1080px;
    padding-top: 100px;
    position: relative;
    margin: 0 auto;

    h1.title {
      font: 72px/1 "genesys";
      text-align: center;
      color: #d70d25;
      margin-bottom: 50px;

      > span {
        color: #222;
      }
    }

    .slide_wrap {
      width: 1460px;
      height: 600px;
      margin: 0 auto;
      position: relative;

      li {
        width: 100%;
        height: 100%;
        position: absolute;
        top: 0;
        left: 0;
        background: gray;
        display: flex;
        justify-content: center;
        align-items: center;
        transition: opacity 0.5s, transform 0.5s;
        cursor: pointer;
        box-shadow: 10px 10px 30px rgba(0, 0, 0, 0.2);

        img {
          width: 100%;
          height: 100%;
          object-fit: cover;
        }

        &:nth-of-type(1) {
          transform: translateX(-20%) scale(0.4);
          opacity: 0;
          z-index: 1;
        }
        &:nth-of-type(2) {
          transform: translateX(20%) scale(0.7);
          opacity: 0;
          // z-index: 1;
        }
        &:nth-of-type(3) {
          width: 980px;
          height: 540px;
          transform: translateX(0) scale(1);
          opacity: 1;
          z-index: 3;
          position: relative;

          &::before {
            content: "\f04b";
            font-family: "Font Awesome 5 Free", "Font Awesome 5 Brands";
            font-weight: 900;
            display: block;
            position: absolute;
            left: 50%;
            top: 50%;
            transform: translate(-50%, -50%);
            font-size: 20px;
            color: #fff;
          }
        }
        &:nth-of-type(4) {
          transform: translateX(16%) scale(0.7);
          z-index: 2;

          &::before {
            content: "";
            display: block;
            width: 100%;
            height: 100%;
            background: rgba(0, 0, 0, 0.5);
            position: absolute;
            left: 0;
            top: 0;
            z-index: 2;
          }
        }
        &:nth-of-type(5) {
          transform: translateX(-20%) scale(0.4);
          opacity: 0;
          z-index: 1;
        }
      }
    }

    .center {
      width: 500px;
      height: 360px;
      background: #fff;
      box-shadow: 10px 10px 20px rgba(0, 0, 0, 0.2);
      position: absolute;
      left: 50%;
      bottom: 8%;
      z-index: 3;
      padding: 60px;

      .article_btn {
        float: left;
        width: 10px;
        height: 10px;
        border-radius: 50%;
        background: gray;
        transition: all 0.4s;

        &:not(:last-child) {
          margin-right: 10px;
        }

        &.on {
          width: 40px;
          background: red;
          border-radius: 10px;
        }
      }

      .article_wrap {
        width: 80%;
        position: absolute;
        left: 60px;
        top: 90px;
        transition: all 0.6s;
        opacity: 0;

        &.on {
          opacity: 1;
        }

        h1 {
          margin-bottom: 20px;
        }

        p {
          font: 16px/1.4 "arial";
          color: #767676;
        }
      }
    }

    .btn_wrap {
      display: flex;
      color: #222;
      position: absolute;
      right: 56%;
      bottom: 18%;
      font: 24px/1 "arial";
      user-select: none;

      div {
        transition: all 0.6s;
        &:hover {
          font-weight: bold;
        }
      }

      .prev {
        cursor: pointer;
        margin-right: 22px;
      }

      .next {
        cursor: pointer;
      }
    }
  }
}
