#Carslide {
  width: 100%;
  height: 100vh;
  overflow: hidden;
  position: relative;
  background: #fff;
  user-select: none;

  .line {
    width: 100%;
    height: 40px;
    background: #000;
    display: flex;
    justify-content: center;
    align-items: center;

    p {
      color: #fff;

      > span {
        color: #d70d25;
      }
    }
  }

  ul.title {
    padding-top: 100px;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    font: 68px/1 "genesys";
    color: #222;
    margin-bottom: 10px;

    li:nth-of-type(7) {
      margin-right: 20px;
    }

    li:nth-of-type(11) {
      margin-right: 20px;
    }
  }

  .sub_title {
    text-align: center;
    margin-bottom: 80px;
  }

  .slider_wrap {
    width: 1200px;
    height: 400px;
    margin: 0 auto;
    position: relative;

    > li {
      width: 100%;
      height: 100%;
      position: absolute;
      top: 0;
      left: 0;
      display: flex;
      justify-content: center;
      align-items: center;
      color: #fff;
      transition: opacity 1s, transform 1s;

      &:nth-of-type(1) {
        transform: translateX(-90%) scale(0.9);
        opacity: 0;
        z-index: 1;
      }
      &:nth-of-type(2) {
        transform: translateX(-70%) scale(0.9);
        opacity: 0.7;
        z-index: 2;
      }
      &:nth-of-type(3) {
        transform: translateX(0) scale(1.5);
        opacity: 1;
        z-index: 3;
      }
      &:nth-of-type(4) {
        transform: translateX(70%) scale(0.9);
        opacity: 0.7;
        z-index: 2;
      }
      &:nth-of-type(5) {
        transform: translateX(-90%) scale(0.9);
        opacity: 0;
        z-index: 1;
      }
    }
  }

  .article_wrap {
    width: 900px;
    display: flex;
    position: absolute;
    left: 50%;
    bottom: 20%;
    transform: translateX(-50%);
    z-index: 10;

    .title {
      width: 100%;
      text-align: center;
      position: relative;

      > h2 {
        width: 500px;
        font: 38px/1 "arial";
        position: absolute;
        left: 50%;
        top: 0;
        transform: translateX(-50%);
        opacity: 0;
        transition: all 0.4s 0.1s;

        &.on {
          opacity: 1;
        }

        > span {
          width: 100%;

          font: 22px/1 "iciel gotham";
          color: #767676;
        }
      }
    }

    // btn
    .prev {
      display: block;
      width: 50px;
      height: 50px;
      position: relative;
      user-select: none;
      cursor: pointer;

      > strong {
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(100%, -50%);
        transition: all 0.4s;
        opacity: 0;
        pointer-events: none;
      }

      &::before {
        content: "";
        display: block;
        width: 100%;
        height: 2px;
        background: #666;
        position: absolute;
        top: 50%;
        left: 0;
        transform: rotate(-35deg);
        transform-origin: left center;
        transition: all 0.4s;
      }

      &::after {
        content: "";
        display: block;
        width: 100%;
        height: 2px;
        background: #666;
        position: absolute;
        top: 50%;
        left: 0;
        transform: rotate(35deg);
        transform-origin: left center;
        transition: all 0.4s;
      }

      &:hover::before {
        top: 0;
        transform: rotate(180deg);
        transform-origin: center center;
      }

      &:hover::after {
        top: 100%;
        transform: rotate(-180deg);
        transform-origin: center center;
      }

      &:hover strong {
        transform: translate(-50%, -50%);
        opacity: 1;
      }
    }

    .next {
      width: 50px;
      height: 50px;
      display: block;
      position: relative;
      cursor: pointer;
      user-select: none;

      &::before {
        display: block;
        content: "";
        width: 100%;
        height: 2px;
        background: #666;
        position: absolute;
        top: 50%;
        right: 0;
        transform: rotate(35deg);
        transform-origin: right center;
        transition: all 0.5s;
      }

      &::after {
        display: block;
        content: "";
        width: 100%;
        height: 2px;
        background: #666;
        position: absolute;
        top: 50%;
        right: 0;
        transform: rotate(-30deg);
        transform-origin: right center;
        transition: all 0.5s;
      }

      > strong {
        position: absolute;
        top: 50%;
        right: 50%;
        transform: translate(-100%, -50%);
        transition: all 0.4s;
        opacity: 0;
        pointer-events: none;
      }

      &:hover::before {
        top: 0;
        transform: rotate(180deg);
        transform-origin: center center;
      }

      &:hover::after {
        top: 100%;
        transform: rotate(-180deg);
        transform-origin: center center;
      }

      &:hover strong {
        transform: translate(50%, -50%);
        opacity: 1;
      }
    }
  }

  .viewbtn {
    width: 260px;
    height: 60px;
    text-transform: uppercase;
    border: 2px solid #970012b7;
    position: relative;
    bottom: -10%;
    left: 50%;
    transform: translate(-50%, 0);
    color: #d70d25;
    text-align: center;
    font: 22px/60px "arial";
    cursor: pointer;
    z-index: 10;
    transition: all 0.5s;
    letter-spacing: 0.1em;

    &:hover {
      box-shadow: 0 8px 20px rgba(0, 0, 0, 0.2);
    }
  }
}
